/* Contenedor general */
body,
html {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #f8f8f8;
  min-height: 100%;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
}

/* Selección de rol */
.role-buttons {
  display: flex;
  gap: 12px;
  margin-top: 20px;
  flex-wrap: wrap;
}

.role-buttons button {
  padding: 8px 16px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  color: white;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.role-buttons button:hover {
  transform: scale(1.05);
}

.role-buttons .admin {
  background-color: #4caf50;
}

.role-buttons .admin:hover {
  background-color: #43a047;
}

.role-buttons .tech1 {
  background-color: #ff9800;
}

.role-buttons .tech1:hover {
  background-color: #fb8c00;
}

.role-buttons .tech2 {
  background-color: #9c27b0;
}

.role-buttons .tech2:hover {
  background-color: #8e24aa;
}

/* Top bar */
.top-bar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin-bottom: 20px;
}

.top-bar h2 {
  margin: 0;
}

.top-bar .logout {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.top-bar .logout:hover {
  background-color: #d32f2f;
}

/* Lista de tickets - Contenedor principal */
.ticket-list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 15px;
  width: 100%;
  max-width: 1200px;
  align-items: flex-start;
}

/* Estilos de la tarjeta de ticket */
.ticket-card,
.ticket {
  background-color: white;
  width: 93%;
  border-radius: 8px;
  padding: 12px;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  transition: transform 0.2s ease, box-shadow 0.2s ease, border-left 0.3s ease;
  min-height: 150px;
}

.ticket-card:hover,
.ticket:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.ticket-header,
.ticket h3 {
  font-weight: bold;
  font-size: 16px;
  margin-bottom: 6px;
}

.ticket-info {
  font-size: 13px;
  margin-bottom: 5px;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.ticket-actions {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 8px;
}

.ticket-actions button,
.ticket-actions select {
  padding: 5px 10px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.2s ease;
}

.ticket-actions button:hover,
.ticket-actions select:hover {
  opacity: 0.85;
}

.ticket-actions .close {
  background-color: #f44336;
  color: white;
  transition: background-color 0.3s ease;
}

.ticket-actions .reopen {
  background-color: #ff9800;
  color: white;
  transition: background-color 0.3s ease;
}

.ticket-actions .description {
  background-color: #607d8b;
  color: white;
  transition: background-color 0.3s ease;
}

/* Estilo para el input multiple (select) */
.ticket-actions select {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  background-color: white;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.ticket-actions select:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

/* Colores según estado */
.ticket-card.open,
.ticket.open {
  border-left: 4px solid #4caf50;
}

.ticket-card.closed,
.ticket.closed {
  border-left: 4px solid #f44336;
}

.ticket-card.reopened,
.ticket.reabierto {
  border-left: 4px solid #ff9800;
}

/* Descripción expandida */
.ticket-description {
  margin-top: 8px;
  font-size: 13px;
  line-height: 1.4;
  padding: 8px;
  border-top: 1px solid #ddd;
  animation: fadeSlide 0.3s ease forwards;
}

@keyframes fadeSlide {
  0% {
    opacity: 0;
    transform: translateY(-5px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Texto cuando no hay tickets */
.no-tickets {
  font-size: 14px;
  color: #555;
  text-align: center;
  margin-top: 10px;
}

/* Diseño responsivo con Media Queries */
@media (min-width: 700px) {
  .ticket-list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1000px) {
  .ticket-list {
    grid-template-columns: repeat(3, 1fr);
  }
}